  //header && footer
  $(function() {

    function bb() {
      return Math.floor(Math.random() * (30000 - 3000 + 1)) + 3000;
    }
    
    let burgerButt = $('li.burger')
        burger = $('#burger')
        navOpenId = $('#navOpen')
        backHeader = $('#backHeader')
        navBack = $('.navBack')
        collectionButton = $('#collectionButton')
        categoryButton = $('#categoryButton')
        collButton = $('.collButton')
        collectionsOpen = $('#collectionsOpen')
        categoryOpen = $('#categoryOpen')
        body = $('body')
        navOpenClass = $('.navOpen')
        addToWish = $('.addToWish')
        footerButton = $('.footerButton');
        collValid = false

        const loungewear = document.getElementById("loungewearButton");
        const jewerly = document.getElementById("jewerlyButton");
        const header = $("header");
        const footer = $("footer");
        const cover = document.getElementById("cover");
    
    let filterButton = $('#filterButton')
        filterCat = $('.filterCat')
        filterContainer = $('.filterContainer')
        
        if($("main").hasClass("oneProductContent")) {
          header.addClass("top")
          collValid = true
        }

        if (!collValid) {
          cover.addEventListener(
            "touchstart",
            function (event) {
              touchstartY = event.changedTouches[0].screenY;
            },
            false
          );
          cover.addEventListener(
            "touchend",
            function (event) {
              touchendY = event.changedTouches[0].screenY;
              handleGesure();
            },
            false
          );
        }
  
        
        function handleGesure() {
          var header = $("header");
          if (touchendY > touchstartY) {
            $(header).removeClass("top")
          }
          if (touchendY < touchstartY) {
            $(header).addClass("top")
          }
        }

      burgerButt.on('click', function () {
        if (!$(this).children().hasClass('burgerOpen')) {
          $(this).children('div').addClass('burgerOpen');
          navOpenId.css('transform', 'translateX(0px)');
          backHeader.addClass('show');
          body.css('overflow', 'hidden');
        } else {
          $(this).children('div').removeClass('burgerOpen');
          navOpenClass.css('transform', 'translateX(-350px)');
          backHeader.removeClass('show');
          body.css('overflow', 'auto');
        }
      });

      loungewear.addEventListener("click", function() {
        header.removeClass("jewerly")
        header.addClass("loungewear")
        footer.removeClass("footerJewerly")
        footer.addClass("footerLoungewear")
      })
      jewerly.addEventListener("click", function() {
        header.addClass("jewerly")
        header.removeClass("loungewear")
        footer.addClass("footerJewerly")
        footer.removeClass("footerLoungewear")
        
      })
  
      collectionButton.click(() => {
        collectionsOpen.css('transform', 'translateX(0px)')
      });
      categoryButton.click(() => {
        categoryOpen.css('transform', 'translateX(0px)')
      });
  
      navBack.children('span').click(function(){
        $(this).parent().parent().css('transform', 'translateX(-350px)')
      });
  
      collButton.children('span').click(function(){
        $(this).next().css('transform', 'translateX(0px)');
        $(this).next().children('.navBack').children().text($(this).text())
      });
  
      backHeader.click(() => {
        navOpenClass.css('transform', 'translateX(-350px)');
        backHeader.removeClass('show');
        burger.removeClass('burgerOpen');
        body.css('overflow', 'auto');
      });
  
      footerButton.click(function(){
        $(this).parent().children('li').toggleClass('show')
      });
  
      function toggleFilter(e) {
        if($(e.target).hasClass('active')) {
          $(e.target).next().removeClass('show')
          $(e.target).removeClass('active')
        } else {
          $(e.target).next().addClass('show')
          $(e.target).addClass('active')
        }
      }
  
      function toggleCat(){  
        if($(this).hasClass('active')) {
          $(this).removeClass('active')
          $(this).next().removeClass('show')
        } else {
          $(this).addClass('active')
          $(this).next().addClass('show')
        }
      }
  
      function closeFilter() {
        document.getElementById('filterInner').classList.remove('show')
        document.getElementById('filterButton').classList.remove('active')
      }
  
      if(!$('main').hasClass('.categoryContent')) {
        filterButton.click((e) => {
          toggleFilter(e);
        })
        filterCat.on('click', '.filterButton', toggleCat);
        filterContainer.on('click', '#closeFilter', closeFilter);
      }
  
      $('.addToWish').click(function() {
        $(this).toggleClass('addedToWish')
      })
      $('.addToWishProduct').click(function() {
        $(this).toggleClass('addedToWish')
      })
  
  
      $('.descriptionInner').children(".title").click(function () {
        $(this).toggleClass('minus')
          $(this).next().toggleClass('show')
      });
  
      
      $("select.qty").on("change", function () {
        $(this).parents('.description').find('.qtyBox').text(this.value)
      });
  
  
      $('.paymentMethod').click(function() {
        $('.paymentMethod').removeClass('selected')
        $(this).addClass('selected')
      })
  
      $('#pageSelected').click(function() {
        $(this).toggleClass('show');
        $(this).next().toggleClass("open")
      })
  });
  
  $(function() {
    let sliderCollectionHome = $('.sliderCollectionHome')
        sliderOneProduct = $('.sliderOneProduct')
    $(document).ready(function(){
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
  
      $('.bannerSlider').slick({
        dots: false,
        infinite: true,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true
      })
      for (let i = 0; i < sliderCollectionHome.length; i++) {
        $(sliderCollectionHome[i]).slick({
          dots: false,
          infinite: true,
          speed: 800,
          slidesToShow: 1,
          slidesToScroll: 1,
          variableWidth: true
        })
      }
      sliderOneProduct.slick({
        infinite: false,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
        vertical: true,
        verticalSwiping: true,
        arrows: false,
        dots: true
      });
      sliderOneProduct.find('.slick-dots').css('top', `calc(25% - ${(sliderOneProduct.find('.slick-dots').children('li').length * 15) / 2}px)`)
  
      
      $(".additional").slick({
        dots: false,
        infinite: true,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true
      })
    });
  });
  
  //drag element 
  
  $(function() {
    if($('main').hasClass('oneProductContent')){
      $('.fuuckSlider').slick({
        dots: false,
        infinite: false,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false
      })
      
      


      var screenHeight = window.innerHeight - 195;
      var dd = document.querySelectorAll(".description");
      var descriptionInner = document.querySelectorAll(".innerContainer");
      var activeLook = true
      var sld = $(".sliderOneProduct").find(".sld");
      var lookItem = $(".lookItem")
      var active = false;
      var currentY;
      var initialY;
      var initialInnerY;
      var yOffset = 0;

      for (let i = 0; i < $(".sliderLook").length; i++ ) {
        $(".sliderLook").eq(i).slick({
          dots: false,
          infinite: true,
          speed: 800,
          slidesToShow: 1,
          slidesToScroll: 1,
          variableWidth: true,
          swipe: true
        })
      }
      
      for (let i = 0; i < dd.length; i++) {
        dd[i].classList.add(`fuuck${i}`)
        descriptionInner[i].classList.add(`bag${i}`)

        $(dd[i]).on("touchstart", function(e) {
          if (e.type === "touchstart") {
            initialY = e.touches[0].clientY - yOffset;
            initialInnerY = e.touches[0].clientY;
          } else {
            initialY = e.clientY - yOffset;
          }
    
          if (!$(`.fuuck${i}`).hasClass('innerActivated')) {
            active = true;
          } else {
            active = false;
          }
        });

        
        $(dd[i]).on("touchend", function(e) {
          if (currentY < -50) {
            $(".fuuckSlider").slick('slickSetOption', 'swipe', false)
            currentY = -screenHeight;
            $(`.fuuck${i}`).addClass('innerActivated')
            $(".oneProductContent").addClass("noArrows")
            setTranslate(currentY, $(`.fuuck${i}`));
          } else if (currentY > -150) {
            $(".fuuckSlider").slick('slickSetOption', 'swipe', true)
            currentY = 0;
            yOffset = 0;
            setTranslate(currentY, $(`.fuuck${i}`));
            closeInnerf ()
          }
          active = false;
        } );


        $(dd[i]).on("touchmove", function(e) {
          if($(`.bag${i}`).scrollTop() == 0 && activeLook == true && $(e.target).parents(".description.innerActivated").length !== 0) {
            if(e.touches[0].clientY > initialInnerY) {
              active = true
            } else {
              active = false
            }
          }
          if (active) {
            e.preventDefault();
            if (e.type === "touchmove") {
              if(e.touches[0].clientY - initialY > -500 && e.touches[0].clientY - initialY < 0)
              currentY = e.touches[0].clientY - initialY;
            } else {
              closeInnerf ()
            }
            $(".sliderOneProduct").slick('slickSetOption', 'swipe', true)
            yOffset = currentY;
            setTranslate(currentY, $(`.fuuck${i}`));
          }
        });
      }

      $(sld).on("touchstart", function(e) {
        if (e.type === "touchstart") {
          initialY = e.touches[0].clientY - yOffset;
          initialInnerY = e.touches[0].clientY;
          active = true
        } else {
          initialY = e.clientY - yOffset;
        }
      });

      $(sld).on("touchmove", function(e) {
          if(e.touches[0].clientY < initialInnerY && $(this).attr("data-slick-index") == $(this).parent().children(".sld:last-child").attr("data-slick-index")) {
            $(".sliderOneProduct").slick('slickSetOption', 'swipe', false)
            currentY = e.touches[0].clientY - initialY;
            active = true
            yOffset = currentY
          } else {
            active = false
            
          }
      })

      $(sld).on("touchend", function(e) {
          if(active) {
            currentY = -screenHeight;
            $(this).parents(".oneProduct").find(".description").addClass('innerActivated')
            $(".oneProductContent").addClass("noArrows")
            setTranslate(currentY, $(this).parents(".oneProduct").find(".description"));
            
          }
          active = false;
      } );

      $(lookItem).on("touchstart", function(e) {
        activeLook = false
      });

      $(lookItem).on("touchend", function(e) {
        activeLook = true
      } );
      $(".closeInner").click(() => closeInnerf());

      $(".sizeButton").click((e) => {
        console.log(e.target)
        $(e.target).parent().parent().parent().find(".sizeContainerProduct").css('bottom', '0px')
      })
  
      $(".sizeCheck").children('input').change((e) => {
        $(".sizeContainerProduct").css('bottom', "-100%");
        $(e.target).parents(".productInner.oneProduct").find(".sizeButton").addClass('sizeChecked')
      })
  
      $(document).on("click", "#sizeValidation", sizeValidationfunc);
  
      function closeInnerf () {

        $(`.description`).removeClass('innerActivated');
        $(".fuuckSlider").slick('slickSetOption', 'swipe', true)
        currentY = 0;
        yOffset = 0;
        $(".oneProductContent").removeClass("noArrows")
        setTranslate(currentY, $(".description"));
        $(`.innerContainer`).scrollTop(0)
      }
  
      function sizeValidationfunc (e) {
        var items = $(e.target).parents('.buttons').next().find('.item');
        
        for (let i = 0; i<=items.length; i++ ) {
          if ($(items[i]).find('input:checked').length != 0) {
            
            continue;
          } else {
            currentY = -screenHeight;
            $(description).addClass('innerActivated')
            setTranslate(currentY, description);
            $(items[i]).find('.chooseSize').addClass('heartBeat')
            break;
          }
        }
      }
  
      function setTranslate(yPos, el) {
        
        el.css("transform", `translate3d(0, ${yPos}px, 0)`)
      }
      }
  });
  
  // resizing 
  window.addEventListener('resize', () => {
    // We execute the same script as before
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  });